body {
  margin: 0;
  font-family: monospace;
}

.header {
  margin-top: 0px;
  overflow: hidden;
  background-color: skyblue;
  border-bottom: 3px solid black;
}

.header {
  list-style-type: none;

  text-align: center;
}

ul.header li a {
  list-style-type: none;
  float: right;
  color: black;
  text-align: center;
  padding: 14px 20px;
  text-decoration: none;

  font-size: 20px;
}

.icon {
  margin-right: 15%;
  float: right;
}

ul.header li a:hover {
  background-color: #ddd;
  color: black;
}

.maincon {
  margin-left: 8%;
  font-family: monospace;
  background-color: rgb(5, 27, 36);
  font-size: 20px;
  float: left;
  margin-top: 175px;
  text-align: center;

  color: white;
  border-radius: 6em;

  width: 800px;
}

.maincon:hover {
  animation: blink 5s infinite;
}

@keyframes maintrans {
  50% {
    font-size: 1000px;
  }
}

.edubox {
  height: auto;
  width: 300px;
  margin: 15px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 45px;
  border: 5px solid black;
  padding: 20px;
  text-align: center;
  font-family: monospace;
  animation-name: myAnimation;
  animation-duration: 1s;
}

.edubox1 {
  height: auto;
  width: 300px;
  margin-left: 5px;
  margin-top: 60px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 45px;
  border: 5px solid black;
  padding: 20px;
  text-align: center;
  font-family: monospace;
  animation-name: myAnimation;
  animation-duration: 1s;
}

.edubox1:hover {
  animation: boxtrans 8s normal forwards;
}

.edubox:hover {
  animation: boxtrans 8s normal forwards;
}
@keyframes boxtrans {
  100% {
    background-color: rgb(9, 189, 212);
    border-radius: 80px;
  }
}

@keyframes myAnimation {
  0% {
    display: none;
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.eduicon {
  width: 75px;
  height: 75px;
  margin-top: 10px;
  border: 1px black solid;
  border-radius: 25px;
}

.eduiconpoly {
  width: 120 px;
  height: 75px;
  margin-top: 10px;
  border: 1px black solid;
  border-radius: 25px;
}

.edu {
  padding-left: 18%;
  padding-top: 5%;
}

.botton {
  background-color: black;
  color: white;
  list-style-type: none;
  text-decoration: none;
  margin-bottom: 5px;
  padding: 10px;
  border: 3px solid white;
  width: 100%;
  border-radius: 5px;
  font-size: 15px;
}

@import url("https://fonts.googleapis.com/css?family=Sacramento&display=swap");

.hihi {
  font-size: calc(5px + 5vh);
  line-height: calc(5px + 5vh);
  /*   text-shadow: 0 0 5px #f562ff, 0 0 15px #f562ff, 0 0 25px #f562ff,
     0 0 20px #f562ff, 0 0 30px #890092, 0 0 80px #890092, 0 0 80px #890092;
   color: #fccaff; */
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500,
    0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
  color: #fff6a9;

  text-align: center;
  animation: blink 12s infinite;
  -webkit-animation: blink 12s infinite;
}

@-webkit-keyframes blink {
  20%,
  24%,
  55% {
    color: #111;
    text-shadow: none;
  }

  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    /*     color: #fccaff;
     text-shadow: 0 0 5px #f562ff, 0 0 15px #f562ff, 0 0 25px #f562ff,
       0 0 20px #f562ff, 0 0 30px #890092, 0 0 80px #890092, 0 0 80px #890092; */
    text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500,
      0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}

@keyframes blink {
  20%,
  24%,
  55% {
    color: #111;
    text-shadow: none;
  }

  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    /*     color: #fccaff;
     text-shadow: 0 0 5px #f562ff, 0 0 15px #f562ff, 0 0 25px #f562ff,
       0 0 20px #f562ff, 0 0 30px #890092, 0 0 80px #890092, 0 0 80px #890092; */
    text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500,
      0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}

.edubox1 a:hover {
  animation: trans 8s normal forwards;
}
@keyframes trans {
  50% {
    border-radius: 30px;
  }
  100% {
    border-radius: 50px;
    color: black;
    background-color: white;
    border: 2px black solid;
  }
}

.progress {
  background: black;
  justify-content: flex-start;
  border-radius: 100px;
  align-items: center;
  position: relative;
  margin-left: 18%;
  display: flex;
  height: 15px;
  width: 200px;
}

.progress-value {
  animation: load 3s normal forwards;

  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}

@keyframes load {
  0% {
    width: 0;
  }

  100% {
    width: 80%;
    background-color: chartreuse;
  }
}

.progress-value1 {
  animation: load1 3s normal forwards;

  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}

@keyframes load1 {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
    background-color: green;
  }
}

.progress-value2 {
  animation: load2 3s normal forwards;

  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}

@keyframes load2 {
  0% {
    width: 0;
  }

  100% {
    width: 70%;
    background-color: orange;
  }
}

.progress-value3 {
  animation: load3 3s normal forwards;

  border-radius: 100px;
  background: grey;
  height: 15px;
  width: 10;
}

@keyframes load3 {
  0% {
    width: 0;
  }

  100% {
    width: 50%;
    background-color: yellow;
  }
}

.skillcard {
  margin-top: 5%;
  margin-left: 10%;
}

.workcard {
  margin-top: 10%;
  margin-left: 10%;
}

.proRJtitle {
  font-size: 60px;
  margin-left: 20px;
}

.probox {
  border: 3px black solid;
  width: 400px;
  text-align: center;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  display: inline-block;
}

.protitle {
  font-size: 30px;
}

.prolink {
  text-decoration: none;
  color: white;

  background-color: black;
  font-size: 70px;
  padding: 5px;
  border-radius: 10px;
}
.proimg {
  width: 5000px;
  height: 500px;
}
